import { useEffect } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, Buttons, SelectBox, CheckBox } from "../../../components/RenderFroms";
import { toast } from 'react-toastify';
import { useFetchByLoad } from "../../../contexts/useFetchByLoad";
import { usePatch } from "../../../contexts/usePatch";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
const resource = "system_settings";

const defaultValue = {
	systemCopyrightTitle: "",
	systemCopyrightUrl: "",
	systemNoOfRecordsInList: "",
	persistantLogin: false,
	systemEmailStatus: "",
	systemEmailSender: "",
	systemEmailBounceEmailId: ""
}

export default function GeneralSettings() {

	const validationSchemaInfo = Yup.object().shape({
		systemCopyrightTitle: Yup.string().required("Name is required"),
		systemCopyrightUrl: Yup.string().required("This is required"),
		systemNoOfRecordsInList: Yup.string().required("This is required"),
		systemEmailStatus: Yup.string().required("This is required"),
		systemEmailBounceEmailId: Yup.string()
			.email("Invalid email address")
			.required("Email is Required"),
		systemEmailSender: Yup.string()
			.email("Invalid email address")
			.required("Email is Required"),

	});

	const { fetch, data, loading: loadingData } = useFetchByLoad();
	useEffect(() => {
		fetch({ url: resource, query: JSON.stringify('query') })
	}, [])

	const { edit, data: respond, loading: loading } = usePatch();
	const handleUpdate = (values: any) => {
		edit(resource, values)
	}

	useEffect(() => {
		if ((respond as any)?.status == 200) {
			toast.success("system settings update successfully");
		}
	}, [respond])

	const statusOptions = [{ label: 'Active', value: 'active' }, { label: 'Disabled', value: 'disabled' }]
	const recordOptions = [{ label: '25', value: '25' }, { label: '50', value: '50' }, { label: '100', value: '100' }, { label: 'All', value: 'all' }]

	if (loadingData) {
		return <>loading</>
	}

	return (
		<>
			<Breadcrumbs root="" pageName="General Settings" />
			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName={`General Settings(* indicates mandatory field(s)).`} />

							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={data ? data : defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (
										<>
											<div className="card-body">

												<InputBox
													required={true}
													name="systemCopyrightTitle"
													lable="Copyright Title"
													placeholder=""
												/>

												<InputBox
													required={true}
													name="systemCopyrightUrl"
													lable="Copyright URL"
													placeholder=""
												/>


												<SelectBox
													options={recordOptions}
													name="systemNoOfRecordsInList"
													lable="Show No. of Records"
													placeholder="Select"
												/>

												<CheckBox
													name="persistantLogin"
													lable="Allow Persistant Login?"
													placeholder=""
												/>


												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-8 col-md-8 col-sm-12 col-xs-12 text-lg blue">Outgoing Emails Related Settings</div>
												</div>


												<SelectBox
													options={statusOptions}
													name="systemEmailStatus"
													lable="Outgoing Email Status"
													placeholder="Select"
												/>

												<InputBox
													required={true}
													name="systemEmailSender"
													lable="System Email Sender's Email ID"
													placeholder=""
												/>

												<InputBox
													required={true}
													name="systemEmailBounceEmailId"
													lable="System Email Default Bounce Email ID"
													placeholder=""
												/>


												<div className="clear"></div>
											</div>
											<div className="card-action">
												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<Buttons loading={loading} className="btn-success" value="Save General Setting" onClick={() => handleSubmit()} />
													</div>
												</div>
												<div className="clear"></div>
											</div>
										</>)}
								</Formik>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}


